
.loading[data-v-59024dbd]{
    position:fixed;
    top:0;
    left:0;
    right:0;
    bottom:0;
    margin:auto;
    width:1rem;
    height:1rem;
    z-index:100;
    background:url(../assets/loading_track.png) no-repeat center;
    background-size:contain;
}
.loading_into[data-v-59024dbd]{
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    -webkit-transform:rotate(0deg);
            transform:rotate(0deg);
    -webkit-animation:load .8s linear infinite;
            animation:load .8s linear infinite;
}
@keyframes load{
from{
        -webkit-transform:rotate(360deg);
                transform:rotate(360deg);
}
to{
        -webkit-transform:rotate(0deg);
                transform:rotate(0deg);
}
}
@-webkit-keyframes load{
from{
        -webkit-transform:rotate(360deg);
                transform:rotate(360deg);
}
to{
        -webkit-transform:rotate(0deg);
                transform:rotate(0deg);
}
}



